.footer {
  background-color: var(--footer-background-color);

  padding: 40px 0;

  @media screen and (min-width: 1024px) {
    padding: 80px 0;

    // background-image: url(../images/footer-line-tablet.png);
    // background-size: cover;
    // background-position: center;
    // background-repeat: no-repeat;
  }

  //   @media screen and (min-width: 1280px) {
  //     background-image: url(../images/footer-lines.png);
  //     background-size: cover;
  //     background-position: center;
  //     background-repeat: no-repeat;
  //   }
}

.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (min-width: 1024px) {
    padding: 0 47px;
  }

  @media screen and (min-width: 1280px) {
    padding: 0 85px;
  }
}

.footer__logo {
  font-family: 'Mimino';
  font-weight: 700;
  font-size: 40px;
  line-height: calc(43 / 40);
  letter-spacing: 0.3em;

  // margin-left: auto;
  // margin-right: auto;

  color: var(--border-button-room);

  &:hover,
  &:focus {
    color: var(--text-secondary-brown);
    @include animation__color;
    cursor: pointer;
  }

  @media screen and (min-width: 414px) {
    margin-left: 44px;
  }

  @media screen and (min-width: 1024px) {
    font-size: 64px;
    line-height: calc(69 / 64);
    letter-spacing: 0.3em;

    margin-left: 107px;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 131px;
  }
}

.footer__menu {
  position: relative;
  &::before {
    @media screen and (min-width: 414px) and (max-width: 1023px) {
      content: '';
      width: 242px;
      height: 1px;
      position: absolute;
      left: -123px;
      bottom: 82px;
      background-color: rgba(122, 122, 122, 0.4);
      filter: blur(0.5px);
      rotate: 90deg;
    }
  }
  @media screen and (min-width: 1024px) {
    display: flex;
    justify-content: space-between;
  }
}

.footer__list {
  // @media screen and (min-width: 414px) {
  //   margin-right: 26px;
  // }

  @media screen and (min-width: 1024px) {
    position: relative;
    margin-right: 137px;
  }
  @media screen and (min-width: 1280px) {
    margin-right: 199px;
  }
}

.footer__list::before {
  @media screen and (min-width: 1024px) {
    content: '';
    width: 310px;
    height: 1px;
    position: absolute;
    left: -162px;
    bottom: 73px;
    background-color: rgba(122, 122, 122, 0.4);
    filter: blur(0.5px);
    rotate: 90deg;
  }

  @media screen and (min-width: 1280px) {
    left: -164px;
  }
}

.footer__item {
  &:not(:last-child) {
    margin-bottom: 16px;
  }
}

.footer__link {
  font-weight: 500;
  font-size: 20px;
  line-height: calc(24 / 20);
  letter-spacing: 0.02em;
  color: var(--border-button-room);

  display: block;

  &:hover,
  &:focus {
    color: var(--text-secondary-brown);
    @include animation__color;
    cursor: pointer;
  }
}

.footer__soc-list {
  display: flex;
  gap: 22px;
  margin-top: 32px;

  @media screen and (min-width: 414px) {
    margin-right: 8px;
  }

  @media screen and (min-width: 1024px) {
    position: relative;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    margin-top: 0;
    margin-right: 76px;
  }
  @media screen and (min-width: 1280px) {
    margin-right: 101px;
  }
}

.footer__soc-list::before {
  @media screen and (min-width: 1024px) {
    content: '';
    width: 310px;
    height: 1px;
    position: absolute;
    left: -168px;
    bottom: 73px;
    background-color: rgba(122, 122, 122, 0.4);
    filter: blur(0.5px);
    rotate: 90deg;
  }

  @media screen and (min-width: 1280px) {
    left: -166px;
  }
}

.footer__soc-link {
  @media screen and (min-width: 1024px) {
    border: 0.5px solid var(--border-button-room);
    padding: 8px 12px;
    width: 143px;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &:hover,
  &:focus {
    border-color: var(--text-secondary-brown);
    @include animation__border;
    cursor: pointer;
  }
}

.footer__soc-text {
  @media screen and (max-width: 1023px) {
    display: none;
  }

  @media screen and (min-width: 1024px) {
    font-size: 16px;
    line-height: calc(20 / 16);
    letter-spacing: 0.02em;
    color: var(--border-button-room);
  }
}

.footer__icon {
  fill: var(--border-button-room);
}

.footer__soc-link:hover .footer__icon,
.footer__soc-link:focus .footer__icon {
  fill: var(--text-secondary-brown);
  @include animation__fill;
}

.footer__soc-link:hover .footer__soc-text,
.footer__soc-link:focus .footer__soc-text {
  color: var(--text-secondary-brown);
  @include animation__color;
}
