.rest-home {
  // background-color: var(--background-section);
  margin: 0;
  position: relative;

  // background-image: url(../images/lines-mobile.png);
  // background-size: cover;
  // background-position: center;
  // background-repeat: no-repeat;

  // @media screen and (min-width: 1024px) {
  //   background-image: url(../images/three-lines.png);
  // }

  &__container--lines {
    &::before {
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      height: 100%;
      box-sizing: inherit;

      @media screen and (max-width: 1023px) {
        background: var(--vertical-lines-mobile);
        width: 136px;
      }

      @media screen and (min-width: 1024px) {
        background: var(--vertical-lines-tablet);
        width: 468px;
      }

      @media screen and (min-width: 1280px) {
        background: var(--vertical-lines-desktop);
        width: 605px;
      }
    }
  }
}

.rest-home__container {
  position: relative;
  @media (min-width: 1024px) {
    padding: 120px 60px 30px 47px;
  }

  @media (min-width: 1280px) {
    padding: 120px 85px 30px 85px;
    width: 1280px;
    margin: 0 auto;
  }
}

.rest-text {
  @media (min-width: 1024px) {
    margin-bottom: 142px;
  }

  @media (min-width: 1280px) {
    margin-bottom: 162px;
  }
}

.rest-home__img {
  margin-bottom: 32px;
  margin-right: 0;
  margin-left: auto;
  box-shadow: 5px 5px 70px 17px rgba(0, 0, 0, 0.25);
  border-radius: 2px;

  @media (min-width: 1024px) {
    content: '';
    position: absolute;
    top: -32px;
    right: 107px;
  }

  @media (min-width: 1280px) {
    right: 180px;
  }
}

.rest-home__img--bg {
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
}

.restor-home__flex {
  position: relative;
  display: flex;
  gap: 20px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;

  @media (min-width: 1024px) {
    margin-bottom: 230px;
  }
}

.rest-text_position {
  @media (min-width: 1024px) {
    content: '';
    position: absolute;
    top: 190px;
    left: 398px;
  }

  @media (min-width: 1280px) {
    left: 492px;
  }
}

.rest-slick {
  @media (min-width: 1024px) {
    position: absolute;
    top: 927px;
    padding: auto;
  }
}

.rest-slick__list {
  display: flex;
  justify-content: center;
  margin-left: 69px;
  padding-bottom: 72px;

  @media (min-width: 1024px) {
    padding-bottom: 96px;
    margin-left: 471px;
    max-width: 428px;
  }

  @media (min-width: 1280px) {
    padding-bottom: 118px;
    margin-left: 560px;
    max-width: 548px;
  }
}

.rest-slick__item {
  margin-right: 24px;
  @media screen and (min-width: 1024px) {
    margin-left: 40px;
    margin-right: 0;
  }
  @media (min-width: 1280px) {
    margin-left: 40px;
    margin-right: 0;
  }
}

.rest-slick__box {
  height: 245px;
  width: 245px;

  @media (min-width: 1024px) {
    height: 194px;
    width: 194px;
  }

  @media (min-width: 1280px) {
    height: 254px;
    width: 254px;
  }
}

.rest-slick__img {
   border-radius: 2px;
}

.my-slick-prev-rest,
.my-slick-next-rest {
  width: 32px;
  height: 32px;
  fill: var(--black-color-room);
  cursor: pointer;
  background-color: #0000;
  border: none;
  padding: 0;
  transition: background-color 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  position: absolute;
  bottom: 20px;

  @media (min-width: 1024px) {
    top: 240px;
  }

  @media (min-width: 1280px) {
    top: 300px;
  }
}

.my-slick-prev-rest {
  background-image: url(../images/rest-home/cil_arrow-left.svg);
  z-index: 3;
  right: 68px;

  @media (min-width: 1024px) {
    left: 330px;
  }

  @media (min-width: 1280px) {
    left: 410px;
  }
}

.my-slick-next-rest {
  background-image: url(../images/rest-home/cil_arrow-right.svg);
  z-index: 3;
  right: 8px;

  @media (min-width: 1024px) {
    left: 390px;
  }

  @media (min-width: 1280px) {
    left: 490px;
  }
}
