.menu-restaurant {
  position: relative;

  background-color: var(--section-reverse-background-color);

  // @media screen and (min-width: 1280px) {
  //   background-image: url(../images/three-lines.png);
  //   background-size: cover;
  //   background-position: center;
  //   background-repeat: no-repeat;
  // }

  &__lines--top {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      height: 100%;

      @media screen and (min-width: 1024px) {
        background: var(--vertical-lines-tablet);
        width: 468px;
      }

      @media screen and (min-width: 1280px) {
        background: var(--vertical-lines-desktop);
        width: 605px;
      }
    }
  }
}

.menu-restaurant__container {
  padding: 32px 15px;
  letter-spacing: 0.02em;

  @media (min-width: 414px) {
    width: 414px;
  }

  @media (min-width: 1024px) {
    width: 1024px;
    display: flex;
    flex-direction: column;
    padding: 58px 48px 40px 47px;
  }

  @media (min-width: 1280px) {
    width: 1280px;
    padding: 58px 85px 60px 85px;
  }
}

.menu-restaurant__title {
  font-family: Times New Roman;
  font-size: 28px;
  line-height: 1.156;
  margin-bottom: 32px;
  text-transform: uppercase;

  @media (min-width: 1024px) {
    margin-bottom: 40px;
  }
}

.menu-restaurant__pages {
  @media (min-width: 1024px) {
    display: flex;
  }
}

.menu-restaurant__first {
  @media (min-width: 1024px) {
    margin-right: 44px;
  }

  @media (min-width: 1280px) {
    margin-right: 70px;
  }
}

.menu-restaurant__second {
  @media (min-width: 1024px) {
    margin-left: 44px;
  }

  @media (min-width: 1280px) {
    margin-left: 70px;
  }
}

.menu-restaurant__first .menu-restaurant__table:last-child {
  @media (min-width: 1024px) {
    margin-bottom: 0;
  }
}

.menu-restaurant__second .table {
  margin-bottom: 16px;

  @media (min-width: 1024px) {
    margin-bottom: 22px;
  }
}

.menu-restaurant__caption {
  font-family: 'Times New Roman';

  font-weight: 700;
  font-size: 16px;
  text-align: left;
  margin-bottom: 16px;

  @media (min-width: 1024px) {
    margin-bottom: 20px;
    font-size: 20px;
  }

  @media (min-width: 1280px) {
    font-size: 24px;
    line-height: 1.167;
  }
}

.menu-restaurant__name {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.214;
  text-align: left;

  width: 40%;

  @media (min-width: 414px) {
    width: 58%;
  }

  @media (min-width: 1024px) {
    width: 54%;
  }

  @media (min-width: 1280px) {
    width: 50%;
    font-size: 16px;
    line-height: 1.25;
  }
}

.menu-restaurant__ingredients {
  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;
  letter-spacing: 0.02em;
  margin-top: 4px;
}

.menu-restaurant__first .menu-restaurant__weight {
  font-size: 14px;
  text-align: center;

  width: 20%;

  @media (min-width: 414px) {
    text-align: left;
    padding-left: 40px;
    width: 25%;
  }

  @media (min-width: 1024px) {
  }

  @media (min-width: 1280px) {
    width: 25%;
    padding-left: 60px;
  }
}

.menu-restaurant__second .menu-restaurant__weight {
  text-align: center;
  width: 20%;

  font-size: 14px;

  @media (min-width: 414px) {
    text-align: left;
    width: 25%;
    padding-left: 40px;
  }

  @media (min-width: 1024px) {
    padding-left: 30px;
    text-align: left;
    width: 21%;
    padding-left: 38px;
  }

  @media (min-width: 1280px) {
    width: 25%;
    padding-left: 60px;
  }
}

.menu-restaurant__price {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.214px;

  text-align: right;
  width: 17%;

  @media (min-width: 414px) {
    width: 20%;
  }

  @media (min-width: 1024px) {
    width: 21%;
  }

  @media (min-width: 1280px) {
    width: 15%;
  }
}

.menu-restaurant__table {
  table-layout: fixed;
  width: 100%;
  border-spacing: 0 8px;

  @media (min-width: 1280px) {
    border-spacing: 0 12px;
  }
}

.menu-restaurant__first .menu-restaurant__table {
  margin-bottom: 16px;

  @media (min-width: 1024px) {
    margin-bottom: 20px;
  }
}

.menu-restaurant__second .menu-restaurant__table {
  margin-bottom: 16px;

  @media (min-width: 1024px) {
    margin-bottom: 20px;
  }
}

.menu-restaurant__bar .menu-restaurant__table {
  border-spacing: 0 11px;

  @media (min-width: 1280px) {
    border-spacing: 0 12px;
  }
}

.menu-restaurant__bar .menu-restaurant__table:last-child {
  margin-bottom: 0;
}

.menu-restaurant__button {
  display: flex;
  font-family: Montserrat;
  margin-top: 32px;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  align-items: center;
  justify-content: center;
  background: #000000;
  cursor: pointer;
  border: 2px solid var(--section-reverse-background-color);
  border-radius: 25px;
  width: 236px;
  height: 49px;
  color: var(--border-button-room);

  font-weight: 700;
  font-size: 16px;
  line-height: 1.25px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media (min-width: 1024px) {
    margin-right: 0;
    margin-top: 40px;
  }

  @media (min-width: 1280px) {
    margin-top: 31px;
  }
}

.menu-restaurant__button:hover,
.menu-restaurant__button:focus {
  background-color: var(--main-white);

  border-color: 2px solid var(--main-white);
}
